<template>
  <div>
    <h1>Register</h1>
    <v-text-field
      type="email"
      v-model="email"
      label="Email"
      required
    ></v-text-field>
    <v-text-field
      type="password"
      v-model="password"
      label="Password"
      required
    ></v-text-field>
    <v-btn @click="register">Register</v-btn>
  </div>
</template>

<script>
import firebase from "firebase";
export default {
  name: "register",
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    register(e) {
      e.preventDefault();
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          user => this.$router.push("/dashboard"),
          err => alert(err.message)
        );
    }
  }
};
</script>

<style></style>



