<template>
  <v-app-bar color="#26a69a" dark app>
    <v-toolbar-title>
      <v-btn text @click="go('/')" id="toolbar-title">
        <v-icon class="icon-padding-right">fa fa-house-user</v-icon>
        <h2>Rent App</h2>
      </v-btn>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-btn text @click="go('/')">Home</v-btn>

      <v-btn v-if="isLoggedIn" text @click="go('/dashboard')">Dashboard</v-btn>
      <v-btn v-if="isLoggedIn" text @click="logout">Logout</v-btn>

      <v-btn v-if="!isLoggedIn" text @click="go('/login')">Login</v-btn>
      <v-btn v-if="!isLoggedIn" text @click="go('/register')">Register</v-btn>
    </v-toolbar-items>
  </v-app-bar>
</template>

<script>
import firebase from "firebase";

export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    go(route) {
      this.$router.push(route);
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => this.$router.push("/"));
    }
  }
};
</script>

<style scoped>
.icon-padding-right {
  padding-right: 10px;
}
</style>



